.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-container {
  text-align: center;
}

.image {
  width: 300px;
  height: auto;
}

.nature-heading {
  font-size: 24px;
  margin: 10px 0;
}

.nature-text {
  font-size: 18px;
  color: gray;
}

.thumbnails-wrapper {
  display: flex;
  align-items: center;
  overflow: hidden;
  width: 300px; /* Adjust as needed */
}

.thumbnails-container {
  display: flex;
  transition: transform 0.3s ease-in-out;
}

.slide-button {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 5px 10px;
  cursor: pointer;
}

.slide-button:hover {
  background-color: #f0f0f0;
}

.thumbnail-item {
  list-style-type: none;
  margin: 0 5px;
}

.thumbnail-button {
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
}

.thumbnail-image {
  width: 50px; /* Adjust the width of the thumbnails */
  height: 50px; /* Adjust the height of the thumbnails */
  object-fit: cover;
}

.active-image {
  border: 2px solid #007bff;
  width: 50px; /* Keep the width consistent */
  height: 50px; /* Keep the height consistent */
}

.active {
  opacity: 0.7;
}
